import React, {Component} from 'react';
import {connect} from 'react-redux';
import HotLinkItem from './hotLinkItem';
import {fetchHotLinks, deleteHotLink} from '../stores/acions/hotlink';

class HotLinkList extends Component{
    state = {
        onLoad: true,
        page: 0
    }
    componentWillMount(){
        this.props.fetchHotLinks(this.state.page).then(()=>{
            this.setState({onLoad: false});
        });
        setTimeout(() => {this.props.fetchHotLinks(this.state.page);}, 2000);
    }
    loadMore = () => {
        this.setState(prevState => ({
            onLoad: true,
            page: prevState.page + 1
        }));
        this.props.fetchHotLinks(this.state.page).then(()=>{
            this.setState({onLoad: false});
        });
    }
    render(){
        const {hotlinks, currentUser, deleteHotLink} = this.props;
        let hotLinkList = hotlinks.map(link => <HotLinkItem 
                                            key={link._id}
                                            text={link.text}
                                            link={link.link}
                                            title={link.title}
                                            description={link.description}
                                            image={link.image}
                                            date={link.createdAt}
                                            userName={link.user.userName}
                                            profileImageUrl={link.user.profileImageUrl}
                                            deleteHotLink={deleteHotLink.bind(this, link.user._id, link._id)}
                                            isCorrectUser={currentUser === link.user._id}
                                            />
                                        );
        return (
            <div className='row col-sm-8'>
                <div className='offset-1 col-sm-10'>
                    {this.state.onLoad && <p>Loading...</p>}
                    <ul className='list-group'>
                        {hotLinkList}
                    </ul>
                    <button onClick={this.loadMore}>Load More</button>
                </div>
            </div>
        );
    }
} 

const mapSteteToProps = (state) => ({
    hotlinks: state.hotLinkReducer,
    currentUser: state.currentUser.user.id
});

export default connect(mapSteteToProps, {fetchHotLinks, deleteHotLink})(HotLinkList);