import {ADD_HOTLINK, REMOVE_HOTLINK} from '../action-types';

export default (state=[], action) => {
    switch(action.type){
        case ADD_HOTLINK:
            return state.concat([...action.hotlinks]);
        case REMOVE_HOTLINK:
            return state.filter(hotlink => hotlink._id !== action.id);
        default:
            return state;
    }
}